@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

li, a, button {
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Poppins', sans-serif;
}

a {
  text-decoration: none;
}
nav {
    z-index: 10;
    position: fixed;
    background: #333;
    height: 75px;
    width: 100%;
    border-bottom: 2px solid #00ffff;
    border-top: 2px solid #e31b6d;
    border-left: 2px solid #e31b6d;
    border-right: 2px solid #00ffff;
}

nav ul {
    float: right;
    margin-right: 20px;
    transition: 0.3s ease-in;
}

nav ul li {
    display: inline-block;
    line-height: 75px;
    margin: 0 5px;
}

nav ul li a {
    color: white;
    font-size: 1.2em;
    border: 2px solid #00ffff;
    padding: 5px 10px;
}

nav ul li a:hover {
    color: #00ffff
}

.checkBtn {
    font-size: 2em;
    color: #00ffff;
    float: right;
    line-height: 80px;
    margin-right: 40px;
    cursor: pointer;
    display: none;
}

#check {
    display: none;
}

@media  screen and (max-width: 700px) {
    nav ul li a {
        font-size: 1em;
    }
}

@media screen and (max-width: 700px) {
    .checkBtn {
        display: block;
    }

    ul {
        z-index: 10;
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 75px;
        left: -100%;
        text-align: center;
        background-color: #333;
    }

    nav ul li {
        display: block;
    }

    nav ul li a {
        font-size: 1.5em;
    }

    #check:checked ~ ul {
        left: 0;
    }
}
.banner {
    background-image: url(/prabhakar/static/media/banner.4bdc4f8e.jpg);
    background-size: cover;
    min-height: 100vh;
    width: 100wh;
    color: #fff;
    margin-bottom: 75px;
}

#intro {
    position: relative;
    top: 250px;
    text-align: center;
    transform: translate(0, -50%);
}

.banner span {
    color: #e31b6d;
}

#intro img {
    margin-top: 100px;
    width: 175px;
    height: 175px;
}

.banner {
    font-size: 1.2em;
    border: 5px solid #00ffff;
}

#work  {
    margin-top: 40px;
    padding: 20px;
    background: none;
    border: none;
}


#work a {
    font-size: 2em;
    padding: 15px;
    color: #fff;
    border:  2px solid #00ffff;
}

 #work:hover a {
     transition: .2s ease-in;
    background-color: #00ffff;
    color: #333;
}


/* media query */

@media screen and (max-width: 350px) {
    .banner {
        height: 100vh;
    }

    .banner h2 {
        display: none;
    }

    #work a {
        padding: 10px;
        font-size: 1.5em;
    }

    #work i {
        display: none;
    }
}


#projects {
    padding-top: 75px;
    margin-top: -75px;
}

.projects h2 {
    text-align: center;
    margin-top: 100px;
    font-size: 3em;
    opacity: 0.6;
}

.project-btns {
    margin-top: 50px;
    margin-bottom: 30px;
    display: flex;
    justify-content: space-around;
    display: flex;
    flex-wrap: wrap;
}

.project-btn {
    margin: 5px;
    text-align: center;
    border: none;
    width: 150px;
    padding: 10px;
    font-size: 1.2em;
    border: 2px solid #e31b6d;
}

.project-btn:hover , .project-btn:focus{
    background-color: #e31b6d;
    color: White;
    transition: .2s ease-in;
}

.active {
    background-color: #e31b6d;
    color: White;
    transition: .2s ease-in;
}

.projects img {
    max-width: 300px;
    maxheight: 250px;
}

.display {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

/* hover effect */

.hover-effect a{
    margin: 0;
    padding: 0;
    text-decoration: none;
}

.hover-effect {
    position: relative;
}

.hover-effect .text {
    text-align: center;
    display: none;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    transition: 0.8s ease-in;
    z-index: 10;
}

.hover-effect:hover .text {
    display: block;
    top: 40%;
    transform: translateX(-50%);
    color:#e31b6d;
    border:2px solid #e31b6d;
    padding: 0 5px;
}

.hover-effect:hover img {
    transition: 0.3s ease-in;
    transform: scale(1.2);
    opacity: 0;
    z-index: 1;
}


@media screen and (max-wodth: 350px) {
    .projects h2 {
        font-size: 1em;
    }
}
.contact {
    background-color: #333;
    height: 100vh;
    padding-top: 75px;
    margin-top: 50px;
}

.contact h2 {
    color: #fff;
    font-size: 3em;
    text-align: center;
    padding-top: 20px;
}

#contact-form {
    width: 600px;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto 0 auto;
    margin-top: 50px;
}

#contact-form input {
    width: 280px;
    margin: 10px;
}

#contact-form textarea {
    width: 580px;
    margin: 0 auto 0 auto;
    height: 100px;
}


#contact-form input, #contact-form textarea, #contact-form button {
    padding: 10px;
    background-color: transparent;
    border: 2px solid #e31b6d;
    color: #fff;
    border-radius: 10px;
}

#contact-form button {
    width: 580px;
    margin: 10px auto 0 auto;
    color: #fff;
    background-color: #e31b6d;
    font-size: 1.2em;
    font-weight: bolder;
    margin-bottom: 40px;
}

#top {
    position: relative;
    left: 50%;
    top: 25px;
    transform: translateX(-50%);
    padding-right: 10px;
    padding-left: 10px;
    font-size: 2.5em;
    background-color: #e31b6d;
    border: none;
}

#top a {
    color: #fff;
}

.social-media {
    width: 100wh;
    background-color: #1b232f;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 50px;
}

.social-media button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    padding: 10px;
    font-size: 1.5em;
    margin: 20px;
    background-color: #333;
    border: none;
    border-radius: 5px;
}

.social-media button a {
    color: #fff;
}

.social-media button:hover, .social-media button:focus {
    background-color: #00ffff;
    transition: .2s ease-in;
}

footer {
    background-color: #1b232f;
    height: 50px;
    text-align: center;
    color: #00ffff
}

footer span {
    color: #e31b6d
}


@media screen and (max-width: 620px) {
    .contact h2  {
        font-size: 2em;
    }

    #contact-form {
        justify-content: left;
        width: 100%;
    }


    #contact-form input, #contact-form textarea {
        width: 100%;
    }

    #contact-form textarea {
        margin: 0 10px;
    }

    #contact-form button {
        margin-right: 20%;
        margin-left: 20%;
        align-self: center;
    }

    .social-media button{
        width: 50px;
    }
}
